export const MakeFieldType = field => {
  if (!field) return null;
  if (
    field.dataType === 'string' &&
    field.editor &&
    field.editor.id === 'multiline'
  ) {
    return 'textarea';
  } else if (
    field.dataType === 'string' &&
    field.editor &&
    field.editor.id === 'list-dropdown'
  ) {
    return 'dropdown';
  } else if (
    (field.editor &&
      field.editor.properties &&
      field.editor.properties.readOnly) ||
    (field.groupId && field.groupId === 'private')
  ) {
    return 'hidden';
  } else if (field.dataType === 'stringArray' || field.dataType === 'boolean') {
    return 'checkbox';
  } else if (
    field.dataType === 'string' &&
    field.validations &&
    field.validations.allowedValues
  ) {
    return 'radio';
  } else if (field.dataType === 'integer') {
    return 'number';
  } else if (field.dataType === 'dateTime') {
    return 'date';
  } else if (field.dataFormat === 'daterange') {
    return 'dateRange';
  } else if (field.dataFormat === 'entry') {
    return 'entryPicker';
  } else {
    return 'textfield';
  }
};
