export const CreateMessage = (
  type,
  minLength,
  maxLength,
  currentLength,
  defaultLanguage
) => {
  if (!type) return null;
  switch (type) {
    case 'min': {
      if (!minLength) return;
      const minLengthVal = minLength && minLength.value;
      const errorMessage =
        minLength && minLength.message && minLength.message[defaultLanguage];
      const minText = `Minimum of ${minLengthVal.toString()} characters`;
      if (errorMessage) {
        return errorMessage;
      } else {
        return minText;
      }
    }
    case 'max': {
      if (!maxLength) return;
      const maxLengthVal = maxLength && maxLength.value;
      const errorMessage =
        maxLength && maxLength.message && maxLength.message[defaultLanguage];
      const maxText = `Maximum of ${maxLengthVal.toString()} characters`;
      if (errorMessage) {
        return errorMessage;
      } else {
        return maxText;
      }
    }
    case 'between': {
      if (!maxLength || !minLength) return;
      const minLengthVal = minLength && minLength.value;
      const maxLengthVal = maxLength && maxLength.value;
      const errorMessage =
        minLength && minLength.message && minLength.message[defaultLanguage];
      const rangeText = `Between ${minLengthVal.toString()}-${maxLengthVal.toString()} characters)`;
      if (errorMessage) {
        return errorMessage;
      } else if (currentLength < minLengthVal) {
        return `${(minLengthVal - currentLength).toString()} character${
          minLengthVal > 1 ? 's' : ''
        } required ${rangeText}`;
      } else if (currentLength > maxLengthVal) {
        return `${(currentLength - maxLengthVal).toString()} character${
          currentLength - maxLengthVal > 1 ? 's' : ''
        } over ${rangeText}`;
      } else if (currentLength) {
        return `${(
          maxLengthVal - currentLength
        ).toString()} characters remaining ${rangeText}`;
      } else {
        return rangeText;
      }
    }
    default:
      return null;
  }
};
