import { createSelector } from 'reselect';
import { getIn } from '@zengenti/contensis-react-base/redux';

export const selectForms = state => getIn(state, ['forms']);
export const selectFormFieldData = (state, formId, field) =>
  getIn(state, ['forms', formId, 'fields', field]);
export const selectFieldErrors = (state, formId) =>
  getIn(state, ['forms', formId, 'fieldErrors']);
export const selectPostData = (state, formId) =>
  getIn(state, ['forms', formId, 'data']);
export const selectFormGroups = (state, formId) =>
  getIn(state, [('forms', formId, 'groups')]);
export const selectPagingInfo = (state, formId) =>
  getIn(state, ['forms', formId, 'pagingInfo']);
export const selectFormStatus = (state, formId) =>
  getIn(state, ['forms', formId, 'status']);
export const selectFormMessage = (state, formId) =>
  getIn(state, ['forms', formId, 'status', 'successMessage']);
export const selectEntries = (state, formId) =>
  getIn(state, ['forms', formId, 'entries']);
export const makeSelectHasSuccess = (state, formId) =>
  getIn(state, ['forms', formId, 'status', 'hasSuccess']);
export const selectPagedFields = formId =>
  createSelector(
    [selectForms],
    form => {
      const pagingInfo = getIn(form, [formId, 'pagingInfo']);
      const fields = getIn(form, [formId, 'fields']);

      if (
        fields &&
        fields.length > 0 &&
        pagingInfo &&
        pagingInfo.pageCount > 1
      ) {
        return fields.filter(f => f.groupId == pagingInfo.currentPageId);
      }
      return fields;
    }
  );
export const makeSelectIsLoading = formId =>
  createSelector(
    selectForms,
    forms => getIn(forms, [formId, 'status', 'isLoading'])
  );
export const makeSelectFormFields = formId =>
  createSelector(
    selectForms,
    forms => getIn(forms, [formId, 'fields'])
  );
export const makeSelectDefaultLang = formId =>
  createSelector(
    selectForms,
    forms => getIn(forms, [formId, 'defaultLanguage'])
  );
