import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

import FormStyled from '../components.styled/Form.styled';
import FormComposer from './FormComposer';
import Button from '../../button';

import { ThemeProvider } from '../../theme/themeContext';
import { defaultTheme } from '../../theme/form';
import { Loading } from './Loading';

const Form = ({
  className,
  formId,
  fields,
  formData,
  setFormId,
  setValue,
  validateField,
  defaultLanguage,
  errors,
  pagingInfo,
  togglePageForward,
  togglePageBack,
  submitForm,
  status,
  setDateRangeValues,
  useDefaultTheme,
  entries,
  customSubmit,
  setCheckboxValue,
}) => {
  useEffect(() => {
    setFormId(formId);
  }, [formId, setFormId]); // NF change rule of hooks

  if (!formId) return null;

  let formRender;
  if (pagingInfo && pagingInfo.pageCount > 1) {
    const isLastPage = pagingInfo.pageCount == pagingInfo.pageIndex + 1;
    formRender = (
      <FormStyled
        className={className}
        id={formId}
        useDefaultTheme={useDefaultTheme}
      >
        {(status && status.isLoading) ||
          (status && status.isSubmitting && <Loading className="loading" />)}
        {!status ||
          (status &&
            !status.isLoading &&
            !status.isSubmitting &&
            !status.hasSuccess && (
              <>
                {pagingInfo.pageIndex > 0 && (
                  <Button
                    type="button"
                    text="Go Back"
                    action={() =>
                      togglePageBack(formId, pagingInfo.pageIndex - 1)
                    }
                    useDefaultTheme={useDefaultTheme}
                  />
                )}
                <FormComposer
                  fields={fields}
                  formData={formData}
                  formId={formId}
                  setValue={setValue}
                  validateField={validateField}
                  defaultLanguage={defaultLanguage}
                  errors={errors}
                  pagingInfo={pagingInfo}
                  useDefaultTheme={useDefaultTheme}
                  entries={entries}
                  setDateRangeValues={setDateRangeValues}
                  setCheckboxValue={setCheckboxValue}
                />
                {!isLastPage && (
                  <Button
                    type="button"
                    text="Next"
                    action={() =>
                      togglePageForward(formId, pagingInfo.pageIndex + 1)
                    }
                    useDefaultTheme={useDefaultTheme}
                  />
                )}
                {isLastPage && (
                  <Button
                    text="Submit"
                    type="button"
                    action={() => {
                      submitForm(formId);
                      if (customSubmit) customSubmit();
                    }}
                    useDefaultTheme={useDefaultTheme}
                  />
                )}
              </>
            ))}
        {status && status.hasSuccess && status.successMessage && (
          <p className="success-message">{status.successMessage}</p>
        )}
      </FormStyled>
    );
  } else {
    formRender = (
      <FormStyled
        className={className}
        id={formId}
        useDefaultTheme={useDefaultTheme}
      >
        {(status && status.isLoading) ||
          (status && status.isSubmitting && <Loading className="loading" />)}
        {!status ||
          (status &&
            !status.isLoading &&
            !status.isSubmitting &&
            !status.hasSuccess && (
              <>
                <FormComposer
                  fields={fields}
                  formId={formId}
                  setValue={setValue}
                  setDateRangeValues={setDateRangeValues}
                  validateField={validateField}
                  defaultLanguage={defaultLanguage}
                  errors={errors}
                  useDefaultTheme={useDefaultTheme}
                  entries={entries}
                  setCheckboxValue={setCheckboxValue}
                />
                <Button
                  text="Submit"
                  type="button"
                  action={() => {
                    submitForm(formId);
                    if (customSubmit) customSubmit();
                  }}
                  useDefaultTheme={useDefaultTheme}
                />
              </>
            ))}
        {status && status.hasSuccess && status.successMessage && (
          <p className="success-message">{status.successMessage}</p>
        )}
      </FormStyled>
    );
  }
  return <ThemeProvider theme={defaultTheme}>{formRender}</ThemeProvider>;
};

Form.propTypes = {
  className: PropTypes.string,
  formId: PropTypes.string,
  fields: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  formData: PropTypes.object,
  entries: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  useDefaultTheme: PropTypes.bool,
  setFormId: PropTypes.func,
  setValue: PropTypes.func,
  setDateRangeValues: PropTypes.func,
  validateField: PropTypes.func,
  defaultLanguage: PropTypes.string,
  errors: PropTypes.array,
  pagingInfo: PropTypes.object,
  togglePageForward: PropTypes.func,
  togglePageBack: PropTypes.func,
  submitForm: PropTypes.func,
  customSubmit: PropTypes.func,
  status: PropTypes.object,
  setCheckboxValue: PropTypes.func,
};

export default Form;
