import React, { useState } from 'react';
import PropTypes from 'prop-types';

import Label from './Label';
import { CharacterLimit, ErrorMessage } from '../../messages';
import styled, { css } from 'styled-components';

const TextfieldStyled = styled.div`
  ${({ isHidden }) => {
    return css`
      display: ${isHidden ? 'none' : 'block'};
    `;
  }};
`;

const Textfield = ({
  className,
  formId,
  id,
  label,
  type,
  setValue,
  validateField,
  validations,
  defaultValue,
  defaultLanguage,
  placeholder,
  isHidden,
  errors,
  useDefaultTheme,
}) => {
  const [showCharLimit, setShowCharLimit] = useState(false);
  const isRequired = validations && validations.required ? true : false;
  const placeholderText =
    placeholder &&
    placeholder.properties &&
    placeholder.properties.placeholderText &&
    placeholder.properties.placeholderText[defaultLanguage];

  const defaultValueText =
    defaultValue && defaultValue[defaultLanguage]
      ? defaultValue[defaultLanguage]
      : defaultValue;
  const _handleChange = (formId, id, value) => {
    setValue(formId, id, value);
  };
  const _handleBlur = (formId, id, value) => {
    validateField(formId, id, value);
    setShowCharLimit(false);
  };
  const _handleFocus = () => {
    setShowCharLimit(true);
  };

  return (
    <TextfieldStyled className="textfield-container" isHidden={isHidden}>
      <Label
        id={id}
        label={label}
        isRequired={isRequired}
        className="label-textfield"
      />
      {showCharLimit && (
        <CharacterLimit
          value={defaultValue}
          validations={validations}
          defaultLanguage={defaultLanguage}
          useDefaultTheme={useDefaultTheme}
        />
      )}
      <input
        className={`${className ? className : ''} input-textfield`}
        type={type}
        defaultValue={defaultValueText}
        placeholder={placeholderText}
        id={id}
        name={id}
        onChange={e => _handleChange(formId, id, e.target.value)}
        onBlur={e => _handleBlur(formId, id, e.target.value)}
        onFocus={() => _handleFocus()}
      />
      {errors && errors.length > 0 && errors.some(x => x.fieldId == id) && (
        <ErrorMessage
          message={errors.find(x => x.fieldId == id).message}
          useDefaultTheme={useDefaultTheme}
        />
      )}
    </TextfieldStyled>
  );
};

Textfield.propTypes = {
  className: PropTypes.string,
  formId: PropTypes.string,
  id: PropTypes.string,
  label: PropTypes.string,
  type: PropTypes.string,
  setValue: PropTypes.func,
  validateField: PropTypes.func,
  validations: PropTypes.object,
  defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  defaultLanguage: PropTypes.string,
  placeholder: PropTypes.object,
  isHidden: PropTypes.bool,
  errors: PropTypes.array,
  useDefaultTheme: PropTypes.bool,
};

export default Textfield;
